.notes-container-wrapper {
    position: fixed;
    width: 90%;
    height: 100%;
    padding-bottom: 80px;
}

.no-notes {
    margin-top: 30px;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    font-size: 25px;
    text-transform: capitalize;
    font-weight: 700;
}

.notes-container-box {
    overflow-y: auto;
   
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;

    gap: 2px;
}

.search-box
{
    border: 2px solid black;
    border-radius: 20px;
    width: 400px;
    height: 40px;
    padding-left: 30px;
}

@media (max-width:450px)  {
    .notes-container-box {
      
        gap: 5px;
    }
}
@media (max-width:768px)  {
    .notes-container-box {
        overflow-y: scroll;
        width: 100%;
        height: 100%;
        display: flex;
        flex-wrap: wrap;
        gap: 20px;
    }
}

 
   
