.note-wrapper {
    margin-top: 30px;
    width: 90%;
    height: 100px;
    display: flex;
   
    border-radius: 15px;
    background-color: rgb(240, 122, 224);
    padding: 10px;
    position: relative;

}

.note-title
{

    padding-top: 10px;
    background-color: transparent;
    border: none;
padding-right: 30px;
    box-sizing: border-box;
}
.note-tag
{
    padding: 10px;
  width: 150px;
  background-color: transparent;
  border: none;
margin-left: 90px;
  box-sizing: border-box;
}
.note-status
{
    width: fit-content;
    height: 50px;
    background-color: transparent;
    border: none;
}
.note-status:focus
{
  
    border: none;
}
.note-wrapper .note-description {
    flex: 1;
    resize: none;
    background-color: transparent;
    border: none;
    box-sizing: border-box;
    margin: 0px 60px;
    padding-top: 10px;
}

textarea:focus {
    outline: none;
}
textarea {
    border: none !important;
}

.note-wrapper textarea {
    font-family: gilroy;
    /* word-spacing: 2px; */
    color:black;
}


.note-bottom
{
    right: 10px;
    top: -35%;
    position: absolute;
    padding: 10px 0px;
    display: flex;
    flex-direction: column;
    gap: 20px;
    text-align: end;
    justify-content: space-between;
    align-items: center;
}
.note-bottom p
{
 /* color: whitesmoke; */
 color: black;
}
.delete-icon
{
    position: absolute;
    right: 10px;
    top: 70px;
    opacity: 0;
width: 25px;
object-fit: cover;
cursor: pointer;
transition: 0.3s ease-in-out;
}
.note-wrapper:hover .delete-icon{
    opacity: 1;
}


/*  responsive design */

@media (max-width:450px) {
    .note-wrapper {
        margin-top: 30px;
        width: 250px;
        height: 250px;
       
    }
}

